.logo {
    width: 140px;
    cursor: pointer;
}

.header-container {
    display: flex;
    flex-direction: row;
    padding: 10px 50px;
    width: 100%;
    margin: 0 auto;
    justify-content: space-between;
    align-items: center;
    background:#161933!important;
    box-sizing: border-box;
}

.top-prices-wrap {
  display: flex;

}

.top-prices {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 100px;
  font-size: 12px;
}

.top-prices p{
  color: rgb(242, 198, 255);
}

.top-prices span {
  font-weight: 700;
}

.connect-button-wrap button {
  background: #0b9b70;
  border-radius: 20px;
  color: rgb(255, 255, 255);
  cursor: pointer;
  outline: none;
  border: none;
}

.connect-button-wrap button:hover {
  background: rgb(255, 255, 255);
  color: rgb(13, 86, 243);
}

.airdrop-icon {
  width: 70px;
  position: absolute;
  right: 23vw;
  -webkit-animation:spin 4s linear ;
    -moz-animation:spin 4s linear ;
    animation:spin 4s linear infinite;
    cursor: pointer;
}

.approved-icon {
  width: 18px;
  margin-right: 0;
  margin-top: 12px;
}

.wallet-icon {
  width: 20px;
  margin-right: 3px;
  margin-top: 12px;
}


.lottery-link-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(rgb(118, 69, 217) 0%, rgb(69, 42, 122) 100%);
  padding: 5px;
  border-radius: 20px;
  padding-right: 30px;
  cursor: pointer;
}

.lottery-link-wrap:hover {
  opacity: 0.7;
  animation: shake 0.5s;

  /* When the animation is finished, start again */
  animation-iteration-count: infinite;
}




.lottery-link-wrap p {
   width: 35px;
   height: 35px;
   background: linear-gradient(rgb(232, 164, 28) 0%, rgb(252, 159, 9) 100%);
   border-radius: 50px;
   display: flex;
   justify-content:center;
   align-items:center;
   font-size: 25px;
   font-weight: 900;
   color: rgb(69, 42, 122);
}


.lottery-link-wrap span {
    margin-left: 30px;
    font-weight: 800;
    font-size: 12px;
}






@keyframes spin { 
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}