.lottery-container {
    width: 60%;
    margin: 0 auto;
    background: rgb(26, 26, 41);
    border-radius: 20px;
    padding-top: 20px;
    color: rgb(49, 49, 49);
    margin-top: 50px;
    font-family: 'Roboto', sans-serif !important;
}


.lottery-body-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px;
    margin-top: 50px;
    color: #e7e6f6;
    box-sizing: border-box;
}

.lottery-main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 70%;
    box-sizing: border-box;
}

.lottery-main h1{
    margin-top: 5px;
    margin-bottom: 30px;
    font-size: 65px;
    color:#35c69a
}

.lottery-main h5{
    font-size: 30px;
 
}


.minting-img {
    width: 30px;
     -webkit-animation:spin 1s linear infinite;
    -moz-animation:spin 1s linear infinite;
    animation:spin 1s linear infinite;
    margin-right: 20px;
}

.loading-div {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
 }
 
 .loading-div img {
     width: 45px;
     -webkit-animation:spin 1s linear infinite;
     -moz-animation:spin 1s linear infinite;
     animation:spin 1s linear infinite;
  }

  .buy-ticket-wrap  {
     display: flex;
     position: relative;
     margin: 50px;
     cursor: pointer;
     animation: swing2 ease-in-out 1s infinite
     alternate;
    transform-origin: center -20px;
    float:left;
     
     }
    

  .buy-ticket-wrap button {
     position: absolute;
     left: 0; 
     right: 0; 
     margin-top: 40px; 
     margin-left: auto; 
     margin-right: auto; 
     width: 230px;
     border-radius: 10px;
     border: none;
     padding: 12px 20px;
     background: linear-gradient(rgb(118, 69, 217) 0%, rgb(69, 42, 122) 100%);
     color: white;
     font-weight: 900;
     font-size: 15px;
     cursor: pointer;
  }

.buy-tickets-btn-big {
    width: 300px;
  
}

.buy-ticket-wrap:hover {
    opacity: 0.7;
    
}


.star-big {
    position: absolute;
    top: 30%;
    width: 100px;
    left: 5%;
    animation: swing ease-in-out 1s infinite
     alternate;
    transform-origin: center -20px;
    opacity: 0.2;
}

.star-small {
    position: absolute;
    top: 0%;
    width: 60px;
    left: 15%;
    animation: swing ease-in-out 1s infinite
     alternate;
    transform-origin: center -20px;
}
.three-stars{
   position: absolute;
   top: 3%;
   width: 90px;
   right: 1%;
   animation: swing ease-in-out 1s infinite
     alternate;
    transform-origin: center -20px;
}
.ticket-l{
    position: absolute;
    top: 68%;
    width: 90px;
    left: 10%;
    animation: swing ease-in-out 1s infinite
     alternate;
    transform-origin: center -20px;
}
.ticket-r{
    position: absolute;
    top: 70%;
    width: 90px;
    right: 7%;
    animation: swing ease-in-out 1s infinite
     alternate;
    transform-origin: center -20px;
}



.lottery-plans-wrap {
    display: flex;
    color: #231C79;
    margin-top: -20px;
    width: 100%;
    background: #231C79;
    padding: 50px;
    background: url(bg.png);
    background-repeat: no-repeat;
    background-size: auto;
    box-sizing: border-box;
}

.lottery-plan {
    background: linear-gradient(#E3ECF8 0%, #9bb7dc 100%);
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    width: 300px;
    height: 420px;
    padding: 10px 20px;
    
}

.plan-rows {
    display: flex;
    justify-content: space-between;
}

.plan-row-6 div {
   width: 50px;
   height: 50px;
   background: linear-gradient(rgb(118, 69, 217) 0%, rgb(69, 42, 122) 100%);
   border-radius: 50px;
   display: flex;
   justify-content: center;
   align-items: center;
   font-size: 35px;
   font-weight: 900;
   color: #E3ECF8;
   margin-top: 5px;
}

.plan-row-6 p {
    font-size: 11px;
    font-weight: 600;
    color: #13112c;
 }

 .plan-row-buy-ticket {
    font-size: 12px;
    margin-top: 30px;
    padding-left: 5px;
 }

 .plan-row-buy-ticket div {
    display: flex;
    align-items: center;

 }

 .plan-row-buy-ticket img {
   width: 20px;
 }

 .plan-row-buy-ticket p {
    opacity: 0;
  }
 
.plan-rows-input {
   border: none;
   outline: none;
   height: 40px;
   border-radius: 10px;
   text-align: right;
   background-color: #aec3df;
   font-size: 45px;
   font-weight: 900;
   padding: 10px 15px;
   margin-top: 5px;
   color: #231C79;
}

.plan-row-value {
    margin-top: 5px;
    
 }

.plan-row-value p {
   font-size: 10px;
   font-style: italic;
   margin-left: 4px;
}

.plan-row-value span {
    font-size: 12px;
    font-weight: 800;
    padding-right: 5px;
    color: #d32fc0;
    
 }

.entries-details {
    padding: 5px;
    margin-top: 30px;

}

.plan-row-entry  {
    margin-top: 9px;
}

.plan-row-entry span {
    font-size: 12px;
}


.plan-row-entry p {
    font-weight: 600;
}

.plan-rows-btn {
    border: none;
    outline: none;
    border-radius: 20px;
    padding: 15px;
    font-size: 20px;
    font-weight: 700;
    margin-top: 50px;
    background: linear-gradient(rgb(118, 69, 217) 0%, rgb(69, 42, 122) 100%);
    color: #E3ECF8;
    cursor: pointer;
}


.plan-rows-btn:hover {
    opacity: 0.7;
}




@keyframes swing2 {
    0% { transform: rotate(3deg); }
    100% { transform: rotate(-3deg); }
}

@keyframes swing {
    0% { transform: rotate(3deg); opacity: 0; }
    100% { transform: rotate(-3deg); opacity: 0;}
}


@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }
